:root {   /* Var callouts*/
  --shadow-color: 0deg 0% 52%;
  --shadow-elevation-medium:
  0px 0.5px 0.5px hsl(var(--shadow-color) / 0.6),
  0px 1.3px 1.2px -1.2px hsl(var(--shadow-color) / 0.5),
  -0.1px 4px 3.8px -2.5px hsl(var(--shadow-color) / 0.4),
  -0.4px 10.9px 10.2px -3.7px hsl(var(--shadow-color) / 0.3);
}

  

  /*CSS RESET*/
  *, *::before, *::after {
    box-sizing: border-box;
  }
  * {
    margin: 0;
  }
  body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
  }
  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }
  input, button, textarea, select {
    font: inherit;
  }
  p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
  }
  #root, #__next {
    isolation: isolate;
  }

  p:focus,
  input:focus,
  select:focus,
  textarea:focus,
  button:focus {
    outline: none;
  }


/*holy grail format*/
.wrapper {
z-index: 0;
display: grid;
grid-template-columns:
  1fr
  min(65ch, 100%)
  1fr;
}
.wrapper > * {
display: grid;
grid-column: 2;
grid-template-columns: 1fr min(5000px, calc(100% - 70px)) 1fr;

}
.full-bleed {
width: 100%;
grid-column: 1 / -1;
}




body{
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-attachment: fixed;
  background-repeat: stretch;
  background-position:center;
  background-size:cover;
}


/*HEADER STUFFS*/
.header{ 
  font-style:normal;
  font-family:Times, 'Times New Roman', serif;
  display:flex;
  justify-content: center;
  align-items: center;
  padding:1.5rem;
  font-size: 20px;
  box-shadow: 0 0.5rem 0.5rem #0c0c0c21, 6px 5px 12px rgba(184, 184, 184, 0.418);
  background-color: rgb(0, 100, 130);
  color: white;
  gap:10px
}




/*MAIN content */


.main-container{
  padding: 20px;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;;
}

/*search STUFFS*/
.city-input{
  border-radius: 5px;
  border: 1px solid gray;
}
.cityBTN{
  background-color: rgb(53, 193, 53);
  border: none;
  border-radius: 50px;
  cursor: pointer;
  box-shadow: 0 2px 0 rgba(128, 128, 128, 0.434)
}
.cityBTN:active{
  background-color: rgb(87, 139, 87);
  border: none;
  border-radius: 50px;
  cursor: pointer;
  box-shadow: none;
  transform: translateY(2px);
}


/*DISPLAY BOX*/
.displayBox{
  font:1 rem;
  margin: 20px;
  border:1px solid rgba(110, 110, 110, 0.521);
  width: 100%;
  border-radius: 20px;;
  box-shadow: var(--shadow-elevation-medium);
  padding: 10px;
  color: balck;
  font-family: Arial, Helvetica, sans-serif;
  text-shadow: var(--shadow-elevation-medium);
  text-align:center;

  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap:1rem;

  overflow:auto;
  overflow: hidden;
  background:linear-gradient(135deg, rgba(255, 255, 255, 0.429), rgba(106, 101, 101, 0.075));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.displayBox > div{
  font:1 rem;
  font-weight: 700px;
  border:none;
  width: 100%;
  border-radius: 8px;;
  padding: 3px;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  text-shadow: var(--shadow-elevation-medium);
  text-align:center;

  display:inline-flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap:10px;

  overflow:auto;
  overflow-y: hidden;
  background:linear-gradient(135deg, rgba(112, 112, 112, 0.618), rgba(0, 0, 0, 0.453));

}

#currentIcon{
  width:50%
}



.gif-container{
  display:flex;
  border-radius: 20px;;
  overflow: hidden;
}

#gifWeather{
  overflow: hidden;
}
  


.hide{
  display:none;
